.Kisinh{
    margin-top: 10vh;
}
.Kisinh-on{
    position: relative;
    display: flex;
    flex-direction: column;
    margin-left: 14vmin;
    margin-right: 14vmin;
    font-size: calc(10px + 2vmin);
    color: white;
    align-items: center;
}
.Kisinh-img{
   position: relative;
   width: 100%;
   max-width: 70vh;
}
.Kisinh-btn{
    background-color: #ffffff47!important;
}
.Kisinh-help{
    width: 100%;
    max-width: 70vh;
    padding: 12px 24px;;
    overflow: hidden;
    border-width: 0;
    outline: none;
    box-shadow: 0 1px 4px rgba(0, 0, 0, .6);
    background-color: #ffffff1c;
    color: #e91e63;
    font-weight: bold;
    transition: background-color .3s;
}
input[type=text] {
    width: 100%;
    max-width: 70vh;
    padding: 5px 10px;
    margin: 8px 0;
    box-sizing: border-box;
  }