.App {
  text-align: center;
  background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
  min-height: 100vh;
  color: white;
}
.gp2{
  color: #2c385e;
  font-weight: bold;
  background-size: 100% 100%;
}
.ks{
  background: linear-gradient(30deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
  background-image: url("./pages/kisinh/bg.jpg");
  background-size: 100% 100%;
}
.knls{
  background-image: url("./pages/knls/bg.jpg");
  color: #2c385e;
  font-weight: bold;
  background-size: 100% 100%;
}

.App-logo {
  max-height: 10vmin;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-btn {
  min-width: 50vmin;
  position: relative;
  display: block;
  margin: 30px auto;
  padding: 0;
  overflow: hidden;
  border-width: 0;
  outline: none;
  box-shadow: 0 1px 4px rgba(0, 0, 0, .6);
  background-color: #0000001c;
  color: #e91e63;
  font-weight: bold;
  transition: background-color .3s;
}
.App-btn:hover {
  background: linear-gradient(-30deg,#13547a 0%, #80d0c7 50%,#13547a 100%);
  color: #ffffff;
  
}
.App-btn > * {
  position: relative;
}
.App-btn span {
  display: block;
  padding: 12px 24px;
}
