.Login-logo {
    height: 40vmin;
    pointer-events: none;
  }
  @media (prefers-reduced-motion: no-preference) {
    .Login-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  .Login-header {
    background-image: linear-gradient(-30deg,#13547a 0%, #80d0c7 50%,#13547a 100%);
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(4px + 2vmin);
    color: white;
  }
  
  .btn {
    width: 100%;
    padding: 12px;
    border: none;
    border-radius: 4px;
    margin: 5px 0;
    opacity: 0.85;
    display: inline-block;
    font-size: 17px;
    line-height: 20px;
    text-decoration: none; /* remove underline from anchors */
  }
  .btn:hover {
    opacity: 1;
  }
  .fb {
    background-color: #3B5998;
    color: white;
  }
  .google {
    background-color: #dd4b39;
    color: white;
  }
  .col {
    min-width: 10vmin;
    padding: 0 50px;
    margin-top: 6px;
  }