.MD-on{
    position: relative;
    display: flex;
    flex-direction: column;
    margin-left: 14vmin;
    margin-right: 14vmin;
    font-size: calc(10px + 2vmin);
    align-items: center;
}
.MD-btn{
    position: relative;
    width: 100%;
    max-width: 70vh;
    margin-top: 2vh;
    padding: 12px 24px;;
    overflow: hidden;
    border-width: 0;
    outline: none;
    box-shadow: 0 1px 4px rgba(0, 0, 0, .6);
    background-color: #ffffff1c;
    color: #e91e63;
    font-weight: bold;
    transition: background-color .3s;
    text-align: left;
}
.MD-btn:hover{
    background-color: #ffffff;
}
.false{
    background-color: #e91e63;
    color: #ffffff;
}
.Kisinh-img{
    position: relative;
    width: 100%;
    max-width: 70vh;
 }
h5{
    color: #2c385e;
    font-weight: bold;
}