.Knls-on{
    position: relative;
    display: flex;
    flex-direction: column;
    margin-left: 14vmin;
    margin-right: 14vmin;
    font-size: calc(10px + 2vmin);
    align-items: center;
}
.Knls-img{
   position: relative;
   width: 100%;
   max-width: 70vh;
}
.Knls-btn{
    width: 100%;
    margin-top: 1rem;
    max-width: 70vh;
    padding: 12px 24px;;
    overflow: hidden;
    border-width: 0;
    outline: none;
    box-shadow: 0 1px 4px rgba(0, 0, 0, .6);
    background-color: #ffffff1c;
    color: #e91e63;
    font-weight: bold;
    transition: background-color .3s;
}
.Knls-btn:hover {
    background-color: #2c385e;
    color: #ffffff;
    
  }